.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: none;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.load-more {
  display: none;
}

.visible {
  display: inline-block;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.input {
  border: 1px solid #000;
  border-radius: 4px;
  outline: 0 solid rgba(0, 0, 0, 0);
  margin-bottom: 8px;
  padding: 4px;
}

.btn-submit {
  color: #fff;
  background-color: #4d5ae5;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-bottom: 4px;
  padding: 6px 10px;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.btn-submit:hover, .btn-submit:focus {
  background-color: #323a94;
}

.load-more {
  color: #fff;
  background-color: #4d5ae5;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 6px 10px;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1);
}

.load-more:hover, .load-more:focus {
  background-color: #323a94;
}

.photo-card {
  width: 300px;
}

.end-results {
  font-size: 36px;
}

