.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  display: none;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.load-more {
  display: none;
}

.visible {
  display: inline-block;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.input {
  margin-bottom: 8px;
  padding: 4;

  border: 1px solid #000;
  border-radius: 4px;
  outline: 0px solid transparent;
}



.btn-submit {
  display: block;

  background-color: #4d5ae5;
  ;
  color: #fff;

  margin-bottom: 4px;
  padding: 6 10;

  border-radius: 4px;
  border-color: transparent;

  transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-submit:hover,
.btn-submit:focus {
  background-color: #323a94;
}

.load-more {
  background-color: #4d5ae5;
  ;
  color: #fff;

  padding: 6 10;

  border-radius: 4px;
  border-color: transparent;

  transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.load-more:hover,
.load-more:focus {
  background-color: #323a94;
}

.photo-card {
  width: 300px;
}

.end-results {
  font-size: 36px;
}